import React from 'react'
import { parseISO, format } from 'date-fns';

// Redux
import { useSelector } from 'react-redux';

// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';

// Form Components
import Gender from './fields/Gender';
import Nationality from './fields/Nationality';
import CivilStatus from './fields/CivilStatus';
import Birthdate from './fields/Birthdate';
import ContactNumber from './fields/ContactNumber';
import { Button, Paper } from '@mui/material';
import { FaSave } from 'react-icons/fa';

export default function ProfileForm() {

    const memberRedux = useSelector((state) => state.members.value);
    let profile = memberRedux.selectedMember;

    const [isEdit, setIsEdit] = React.useState(false);

    // Basic Profile  
    const [lastName, setLastName] = React.useState(profile.last_name ?? "");
    const [firstName, setFirstName] = React.useState(profile.first_name ?? "");
    const [middleName, setMiddleName] = React.useState(profile.middle_name ?? "");
    const [birthdate, setBirthdate] = React.useState(parseISO(profile.birthdate));
    const [gender, setGender] = React.useState(profile.gender[0] ?? "");
    const [nationality, setNationality] = React.useState(profile.nationality ?? "");
    const [civilStatus, setCivilStatus] = React.useState(profile.civil_status ?? "");
    const [contactNumber, setContactNumber] = React.useState(profile.contact_no.replace("+63", "") ?? "");
    const [fbLink, setFbLink] = React.useState(profile.fb_link ?? "");

    const handleSubmit = (e) => {
        e.preventDefault();
        setIsEdit(false);
    }

    return (
        <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
            <div>
                <Paper sx={{ p: 5, m: 1 }}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} sx={{ mb: 1 }}>
                            <FormControlLabel sx={{ mr: 1 }} control={<Switch checked={isEdit} onChange={() => setIsEdit(!isEdit)} size="small" sx={{ ml: 1 }} />} label="Edit" />
                            {isEdit &&
                                <React.Fragment>
                                    <Button
                                        sx={{
                                            pt: 0,
                                            pb: 0,
                                            width: 75,
                                            '&:hover': {
                                                backgroundColor: 'transparent'
                                            },
                                            '&:before': {
                                                content: "'|'",
                                                ml: -1,
                                                mr: 1,
                                                color: '#000'
                                            },
                                            fontSize: 14,
                                            color: '#1976D2'
                                        }}
                                        type="submit"
                                        fullWidth
                                        variant="text"
                                        color="primary"
                                    >
                                        <FaSave size={14} style={{ marginRight: 5 }} />
                                        Save
                                    </Button>
                                </React.Fragment>
                            }
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setLastName(e.target.value)}
                                value={lastName}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="lastName"
                                label="Last Name"
                                placeholder="Last Name"
                                name="lastName"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setFirstName(e.target.value)}
                                value={firstName}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="firstName"
                                label="First Name"
                                placeholder="First Name"
                                name="firstName"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setMiddleName(e.target.value)}
                                value={middleName}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="middleName"
                                label="Middle Name"
                                placeholder="Middle Name"
                                name="middleName"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={4}>
                            {!isEdit &&
                                <TextField
                                    value={format(birthdate, "MMM dd, yyyy")}
                                    variant="standard"
                                    size="small"
                                    fullWidth
                                    label="Birthdate"
                                    inputProps={
                                        { readOnly: true }
                                    }
                                />
                            }
                            {isEdit &&
                                <Birthdate setBirthdate={setBirthdate} birthdate={birthdate} variant="standard" />
                            }
                        </Grid>
                        <Grid item xs={4}>
                            {!isEdit &&
                                <TextField
                                    value={gender.toLowerCase() === "m" ? "Male" : "Female"}
                                    variant="standard"
                                    size="small"
                                    fullWidth
                                    label="Gender"
                                    inputProps={
                                        { readOnly: true }
                                    }
                                />
                            }
                            {isEdit &&
                                <Gender setGender={setGender} gender={gender} variant="standard" isRequired={isEdit} />
                            }
                        </Grid>
                        <Grid item xs={4}>
                            {!isEdit &&
                                <TextField
                                    value={nationality}
                                    variant="standard"
                                    size="small"
                                    fullWidth
                                    label="Nationality"
                                    inputProps={
                                        { readOnly: true }
                                    }
                                />
                            }
                            {isEdit &&
                                <Nationality setNationality={setNationality} nationality={nationality} variant="standard" />
                            }
                        </Grid>
                        <Grid item xs={4}>
                            {!isEdit &&
                                <TextField
                                    value={civilStatus}
                                    variant="standard"
                                    size="small"
                                    fullWidth
                                    label="Civil Status"
                                    inputProps={
                                        { readOnly: true }
                                    }
                                />
                            }
                            {isEdit &&
                                <CivilStatus setCivilStatus={setCivilStatus} civilStatus={civilStatus} variant="standard" />
                            }
                        </Grid>
                        <Grid item xs={4}>
                            {!isEdit &&
                                <TextField
                                    value={`+63${contactNumber}`}
                                    variant="standard"
                                    size="small"
                                    fullWidth
                                    label="Contact Number"
                                    inputProps={
                                        { readOnly: true }
                                    }
                                />
                            }
                            {isEdit &&
                                <TextField
                                    onChange={(e) => setContactNumber(e.target.value)}
                                    value={contactNumber}
                                    variant="standard"
                                    size="small"
                                    required
                                    fullWidth
                                    id="contactNumber"
                                    label="Contact Number"
                                    name="contactNumber"
                                    InputProps={{
                                        inputComponent: ContactNumber
                                    }}
                                />
                            }
                        </Grid>
                        <Grid item xs={4}>
                            {!isEdit &&
                                <TextField
                                    value={fbLink}
                                    variant="standard"
                                    size="small"
                                    fullWidth
                                    label="FaceBook Link"
                                    inputProps={
                                        { readOnly: true }
                                    }
                                />
                            }
                            {isEdit &&
                                <TextField
                                    onChange={(e) => setFbLink(e.target.value)}
                                    value={fbLink}
                                    variant="standard"
                                    size="small"
                                    required
                                    fullWidth
                                    id="fbLink"
                                    label="FaceBook Link"
                                    name="fbLink"
                                />
                            }
                        </Grid>
                    </Grid>
                </Paper>
            </div>
        </form>
    )
}
