import React from 'react';

// Redux
import { useSelector } from 'react-redux';

// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';

// Form Components 
import { Button, Paper } from '@mui/material';
import { FaSave } from 'react-icons/fa';

export default function ParentsForm(props) {

    const memberRedux = useSelector((state) => state.members.value);
    let family = memberRedux.selectedMember.family;

    const [isEdit, setIsEdit] = React.useState(false);

    const [fathersName, setFathersName] = React.useState(family[0]?.name ?? "");
    const [mothersName, setMothersName] = React.useState(family[1]?.name ?? "");

    const handleSubmit = (e) => {
        e.preventDefault();
    }

    return (
        <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
            <div>
                <Paper elevation={0} square sx={{ p: 1, m: 1 }}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} sx={{ mb: 1 }}>
                            <FormControlLabel sx={{ mr: 1 }} control={<Switch checked={isEdit} onChange={() => setIsEdit(!isEdit)} size="small" sx={{ ml: 1 }} />} label="Edit" />
                            {isEdit &&
                                <React.Fragment>
                                    <Button
                                        sx={{
                                            pt: 0,
                                            pb: 0,
                                            width: 75,
                                            '&:hover': {
                                                backgroundColor: 'transparent'
                                            },
                                            '&:before': {
                                                content: "'|'",
                                                ml: -1,
                                                mr: 1,
                                                color: '#000'
                                            },
                                            fontSize: 14,
                                            color: '#1976D2'
                                        }}
                                        type="submit"
                                        fullWidth
                                        variant="text"
                                        color="primary"
                                    >
                                        <FaSave size={14} style={{ marginRight: 5 }} />
                                        Save
                                    </Button>
                                </React.Fragment>
                            }
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                onChange={(e) => setFathersName(e.target.value)}
                                value={fathersName}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="fathersName"
                                label="Father's Name"
                                name="fathersName"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                onChange={(e) => setMothersName(e.target.value)}
                                value={mothersName}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="mothersName"
                                label="Mother's Name"
                                name="mothersName"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                    </Grid>
                </Paper>
            </div>
        </form>
    )
}
