import React from 'react'
import utilityService from '../../../services/utils/utilityServices';

// Redux
import { useSelector } from 'react-redux';

// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';
import Button from '@mui/material/Button';
import Paper from '@mui/material/Paper';

// Form Components 
import EmploymentTypes from './fields/EmploymentTypes';
import OrganizationTypes from './fields/OrganizationTypes';
import YearOrMonth from './fields/YearOrMonth';
import Country from './fields/Country';
import NoData from '../shared/NoData/NoData';
import Box from '@mui/material/Box';

// react-icons
import { FaSave, FaTimes } from 'react-icons/fa';

export default function EmploymentForm() {

    const memberRedux = useSelector((state) => state.members.value);
    let withData = memberRedux.selectedMember.employment.length;
    let employment = {};
    let serviceLength = ["", ""];

    if (withData > 0) {
        employment = memberRedux.selectedMember.employment[0];
        serviceLength = employment.service_length.split(" ");
    }

    const [isEdit, setIsEdit] = React.useState(false);

    // Work or Business Financial Details 
    const [occupation, setOccupation] = React.useState(employment.position ?? "");
    const [employmentType, setEmploymentType] = React.useState(employment?.employment_type_id?.toString() ?? '');
    const [organizationType, setOrganizationType] = React.useState(employment?.organization_type_id?.toString() ?? '');
    const [lengthInService, setLengthInService] = React.useState(serviceLength[0] ?? "");
    const [yearOrMonth, setYearOrMonth] = React.useState(utilityService.capitalizeFirstLetter(serviceLength[1]) ?? "");
    const [companyName, setCompanyName] = React.useState(employment.company ?? "");
    const [companyAddress, setCompanyAddress] = React.useState(employment.address ?? "");
    const [salary, setSalary] = React.useState(employment.salary ?? "");
    const [country, setCountry] = React.useState(employment.country ?? "");

    const handleSubmit = (e) => {
        e.preventDefault();
        setIsEdit(false);
    }

    const handleChangeLengthInService = (value, spouse = false) => {

        let len = parseInt(value);
        let label = yearOrMonth;
        let lastChar = label.slice(label.length - 1);

        // Checking if plural or not
        if (isFinite(len) && len === 1) {

            if (lastChar.toLowerCase() === 's') {
                label = label.slice(0, -1);
            }

        } else if (isFinite(len) && len > 1) {

            if (lastChar.toLowerCase() !== 's') {
                label = `${label}s`;
            }

        }

        setLengthInService(value);
        setYearOrMonth(label);

    }

    return (
        <React.Fragment>
            {withData <= 0 &&
                <Box sx={{ height: 200 }}>
                    <NoData title="No Work Details Found" />
                </Box>
            }
            {withData > 0 &&
                <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
                    <div>
                        <Paper sx={{ p: 5, m: 1 }}>
                            <Grid container spacing={2}>
                                <Grid item xs={12} sx={{ mb: 3 }}>
                                    <FormControlLabel sx={{ mr: 1 }} control={<Switch checked={isEdit} onChange={() => setIsEdit(!isEdit)} size="small" sx={{ ml: 1 }} />} label="Edit" />
                                    {isEdit &&
                                        <React.Fragment>
                                            <Button
                                                sx={{
                                                    pt: 0,
                                                    pb: 0,
                                                    width: 105,
                                                    '&:hover': {
                                                        backgroundColor: 'transparent'
                                                    },
                                                    '&:before': {
                                                        content: "'|'",
                                                        ml: 0,
                                                        mr: 2,
                                                        color: '#000'
                                                    },
                                                    fontSize: 14,
                                                    color: '#1976D2'
                                                }}
                                                type="submit"
                                                fullWidth
                                                variant="text"
                                                color="primary"
                                            >
                                                <FaSave size={14} style={{ marginRight: 5 }} />
                                                Save
                                            </Button>
                                            <Button
                                                sx={{
                                                    pt: 0,
                                                    pb: 0,
                                                    width: 110,
                                                    '&:hover': {
                                                        backgroundColor: 'transparent'
                                                    },
                                                    '&:before': {
                                                        content: "'|'",
                                                        ml: 0,
                                                        mr: 2,
                                                        color: '#000'
                                                    },
                                                    fontSize: 14,
                                                    color: '#E45570'
                                                }}
                                                type="submit"
                                                fullWidth
                                                variant="text"
                                                color="secondary"
                                            >
                                                <FaTimes size={14} style={{ marginRight: 5 }} />
                                                Remove
                                            </Button>
                                        </React.Fragment>
                                    }
                                </Grid>
                                <Grid item xs={4}>
                                    <EmploymentTypes
                                        setEmploymentType={setEmploymentType}
                                        employmentType={employmentType}
                                        isEdit={!isEdit}
                                        variant="standard"
                                    />
                                </Grid>
                                <Grid item xs={4}>
                                    <OrganizationTypes
                                        setOrganizationType={setOrganizationType}
                                        organizationType={organizationType}
                                        isEdit={!isEdit}
                                        variant="standard"
                                    />
                                </Grid>
                                <Grid item xs={4}>
                                    <TextField
                                        onChange={(e) => setCompanyName(e.target.value)}
                                        value={companyName}
                                        variant="standard"
                                        size="small"
                                        required={isEdit}
                                        fullWidth
                                        id="companyName"
                                        label="Company"
                                        name="companyName"
                                        inputProps={
                                            { readOnly: !isEdit }
                                        }
                                    />
                                </Grid>
                                <Grid item xs={4}>
                                    <TextField
                                        onChange={(e) => setCompanyAddress(e.target.value)}
                                        value={companyAddress}
                                        variant="standard"
                                        size="small"
                                        required={isEdit}
                                        fullWidth
                                        id="companyAddress"
                                        label="Company Address"
                                        name="companyAddress"
                                        inputProps={
                                            { readOnly: !isEdit }
                                        }
                                    />
                                </Grid>
                                <Grid item xs={4}>
                                    <TextField
                                        onChange={(e) => setOccupation(e.target.value)}
                                        value={occupation}
                                        variant="standard"
                                        size="small"
                                        required={isEdit}
                                        fullWidth
                                        id="occupation"
                                        label="Occupation"
                                        name="occupation"
                                        inputProps={
                                            { readOnly: !isEdit }
                                        }
                                    />
                                </Grid>
                                <Grid item xs={4}>
                                    <TextField
                                        onChange={(e) => setSalary(e.target.value)}
                                        value={salary}
                                        variant="standard"
                                        size="small"
                                        required={isEdit}
                                        fullWidth
                                        id="salary"
                                        label="Salary"
                                        name="salary"
                                        inputProps={
                                            { readOnly: !isEdit }
                                        }
                                    />
                                </Grid>
                                <Grid item xs={4}>
                                    <Country
                                        country={country}
                                        setCountry={setCountry}
                                        isEdit={!isEdit}
                                        variant="standard"
                                    />
                                </Grid>
                                <Grid item xs={4}>
                                    <YearOrMonth
                                        serviceLength={lengthInService}
                                        setServiceLength={(value) => handleChangeLengthInService(value, false)}
                                        yearOrMonth={yearOrMonth}
                                        setYearOrMonth={setYearOrMonth}
                                        isEdit={!isEdit}
                                        variant="standard"
                                    />
                                </Grid>
                            </Grid>
                        </Paper>
                    </div>
                </form>
            }
        </React.Fragment>
    )
}
