import React from 'react';
import utilityService from '../../../services/utils/utilityServices';

// Redux
import { useSelector } from 'react-redux';

// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';

// Page Components
import YearOrMonth from './fields/YearOrMonth';
import ContactNumber from './fields/ContactNumber'; 

// Form Components 
import { Button, Paper } from '@mui/material';
import { FaSave, FaTimes } from 'react-icons/fa';

export default function SpouseForm(props) {

    const memberRedux = useSelector((state) => state.members.value);
    let spouse = memberRedux.selectedMember.spouse;
    let yearOrMonth = spouse?.length_of_service.split(" ") ?? [];

    const [isEdit, setIsEdit] = React.useState(false);

    const [spouseName, setSpouseName] = React.useState(spouse?.name ?? "");
    const [spouseOccupation, setSpouseOccupation] = React.useState(spouse?.occupation ?? "");
    const [spouseContactNumber, setSpouseContactNumber] = React.useState(spouse?.contact_no ?? "");
    const [spouseLengthInService, setSpouseLengthInService] = React.useState(yearOrMonth[0]  ?? "");
    const [spouseYearOrMonth, setSpouseYearOrMonth] = React.useState(yearOrMonth[1] ?? "");

    const handleSubmit = (e) => {
        e.preventDefault();
    }

    const handleChangeLengthInService = (value, spouse = false) => {

        let len = parseInt(value);
        let label = spouseYearOrMonth;
        let lastChar = label.slice(label.length - 1);

        // Checking if plural or not
        if (isFinite(len) && len === 1) {

            if (lastChar.toLowerCase() === 's') {
                label = label.slice(0, -1);
            }

        } else if (isFinite(len) && len > 1) {

            if (lastChar.toLowerCase() !== 's') {
                label = `${label}s`;
            }

        }

        setSpouseLengthInService(value);
        setSpouseYearOrMonth(label);

    }

    return (
        <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
            <div>
                <Paper elevation={0} square sx={{ p: 1, m: 1 }}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} sx={{ mb: 1 }}>
                            <FormControlLabel sx={{ mr: 1 }} control={<Switch checked={isEdit} onChange={() => setIsEdit(!isEdit)} size="small" sx={{ ml: 1 }} />} label="Edit" />
                            {isEdit &&
                                <React.Fragment>
                                    <Button
                                        sx={{
                                            pt: 0,
                                            pb: 0,
                                            width: 105,
                                            '&:hover': {
                                                backgroundColor: 'transparent'
                                            },
                                            '&:before': {
                                                content: "'|'",
                                                ml: 0,
                                                mr: 2,
                                                color: '#000'
                                            },
                                            fontSize: 14,
                                            color: '#1976D2'
                                        }}
                                        type="submit"
                                        fullWidth
                                        variant="text"
                                        color="primary"
                                    >
                                        <FaSave size={14} style={{ marginRight: 5 }} />
                                        Save
                                    </Button>
                                    <Button
                                        sx={{
                                            pt: 0,
                                            pb: 0,
                                            width: 110,
                                            '&:hover': {
                                                backgroundColor: 'transparent'
                                            },
                                            '&:before': {
                                                content: "'|'",
                                                ml: 0,
                                                mr: 2,
                                                color: '#000'
                                            },
                                            fontSize: 14,
                                            color: '#E45570'
                                        }}
                                        type="submit"
                                        fullWidth
                                        variant="text"
                                        color="secondary"
                                    >
                                        <FaTimes size={14} style={{ marginRight: 5 }} />
                                        Remove
                                    </Button>
                                </React.Fragment>
                            }
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                onChange={(e) => setSpouseName(e.target.value)}
                                value={spouseName}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="spouseName"
                                label="Spouse Name"
                                name="spouseName"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                onChange={(e) => setSpouseContactNumber(e.target.value)}
                                value={spouseContactNumber}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="spouseContactNumber"
                                label="Spouse Mobile No."
                                name="spouseContactNumber"
                                InputProps={{
                                    inputComponent: ContactNumber
                                }}
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                onChange={(e) => setSpouseOccupation(e.target.value)}
                                value={spouseOccupation}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="spouseOccupation"
                                label="Spouse Occupation"
                                name="spouseOccupation"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <YearOrMonth
                                serviceLength={spouseLengthInService}
                                setServiceLength={(value) => handleChangeLengthInService(value, true)}
                                yearOrMonth={utilityService.capitalizeFirstLetter(spouseYearOrMonth)}
                                setYearOrMonth={setSpouseYearOrMonth}
                                isEdit={!isEdit}
                                variant="standard"
                            />
                        </Grid>
                    </Grid>
                </Paper>
            </div>
        </form>
    )
}
