import React from 'react'

// Redux
import { useSelector } from 'react-redux';

// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';

// Form Components 
import { Button, Paper } from '@mui/material';
import { FaSave } from 'react-icons/fa';

export default function PresentAddressForm(props) {

    const memberRedux = useSelector((state) => state.members.value);
    let address = memberRedux.selectedMember.address;

    const [isEdit, setIsEdit] = React.useState(false);

    // Present Address 
    const [presentZipcode, setPresentZipcode] = React.useState(address[1]?.zipcode ?? "");
    const [presentStreet, setPresentStreet] = React.useState(address[1]?.streetpurok ?? "");
    const [presentBrgy, setPresentBrgy] = React.useState(address[1]?.barangay ?? "");
    const [presentCity, setPresentCity] = React.useState(address[1]?.city ?? "");
    const [presentProvince, setPresentProvince] = React.useState(address[1]?.province ?? "");

    const handleSubmit = (e) => {
        e.preventDefault();
    }

    return (
        <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
            <div>
                <Paper elevation={0} square sx={{ p: 1, m: 1 }}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} sx={{ mb: 1 }}>
                            <FormControlLabel sx={{ mr: 1 }} control={<Switch checked={isEdit} onChange={() => setIsEdit(!isEdit)} size="small" sx={{ ml: 1 }} />} label="Edit" />
                            {isEdit &&
                                <React.Fragment>
                                    <Button
                                        sx={{
                                            pt: 0,
                                            pb: 0,
                                            width: 75,
                                            '&:hover': {
                                                backgroundColor: 'transparent'
                                            },
                                            '&:before': {
                                                content: "'|'",
                                                ml: -1,
                                                mr: 1,
                                                color: '#000'
                                            },
                                            fontSize: 14,
                                            color: '#1976D2'
                                        }}
                                        type="submit"
                                        fullWidth
                                        variant="text"
                                        color="primary"
                                    >
                                        <FaSave size={14} style={{ marginRight: 5 }} />
                                        Save
                                    </Button>
                                </React.Fragment>
                            }
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setPresentStreet(e.target.value)}
                                value={presentStreet}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="street"
                                label="Street/Purok/Block/Zone"
                                name="street"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setPresentBrgy(e.target.value)}
                                value={presentBrgy}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="brgy"
                                label="Barangay"
                                name="brgy"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setPresentCity(e.target.value)}
                                value={presentCity}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="city"
                                label="City"
                                name="city"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setPresentProvince(e.target.value)}
                                value={presentProvince}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="province"
                                label="Province"
                                name="province"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setPresentZipcode(e.target.value)}
                                value={presentZipcode}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="zipcode"
                                label="Zipcode"
                                name="zipcode"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                    </Grid>
                </Paper>
            </div>
        </form>
    )
}
